.divide-line {
    position: relative;
    margin-bottom: 2.4rem;
    z-index: 1
}

.divide-line__vertical {
    position: absolute;
    width: 2px;
    height: 3.6rem;
    background: linear-gradient(180deg,#1668b2 50%,rgba(22,104,178,0) 0);
    background-size: 100% 200%;
    background-position: 0 -3.6rem;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
    -webkit-animation: scrolldown 3s cubic-bezier(.76,0,.3,1) infinite forwards;
    animation: scrolldown 3s cubic-bezier(.76,0,.3,1) infinite forwards
}

@-webkit-keyframes scrolldown {
    0% {
        background-position: 0 -3.6rem
    }

    75% {
        background-position: 0 0
    }

    to {
        background-position: 0 3.6rem
    }
}

@keyframes scrolldown {
    0% {
        background-position: 0 -3.6rem
    }

    75% {
        background-position: 0 0
    }

    to {
        background-position: 0 3.6rem
    }
}

@media screen and (min-width: 576px) {
    .divide-line {
        margin-bottom:5rem
    }

    .divide-line__vertical {
        height: 10rem;
        background-position: 0 -10rem;
        -webkit-animation: scrolldown 2.2s cubic-bezier(.76,0,.3,1) infinite forwards;
        animation: scrolldown 2.2s cubic-bezier(.76,0,.3,1) infinite forwards
    }

    @-webkit-keyframes scrolldown {
        0% {
            background-position: 0 -10rem
        }

        75% {
            background-position: 0 0
        }

        to {
            background-position: 0 10rem
        }
    }

    @keyframes scrolldown {
        0% {
            background-position: 0 -10rem
        }

        75% {
            background-position: 0 0
        }

        to {
            background-position: 0 10rem
        }
    }
}

